<template>
  <x-header :left-options="{showBack: false}"  :right-options="{showMore: true}"  @on-click-more="showMenus = true">待交易列表</x-header>
  <group>
    <template v-for="item in items">
      <cell :title="item.name" :inline-desc="item.location" is-link :link="getlink(item.id)">￥ {{ item.price }}</cell>
    </template>
  </group>
  <divider></divider>
  <divider></divider>
  <actionsheet :menus="menus" :show.sync="showMenus" show-cancel @on-click-menu="click" cancel-text="取消"></actionsheet>
  <alert :show.sync="show" title="联系我们" button-text="知道了">
    <p style="text-align:center;">如有使用问题，请发送邮件到<br>noahgaocn@gmail.com</p>
    <p style="text-align:center;">如有交易、回收问题，请联系<br>天津科技大学<br>电子信息与自动化学院<br>青年志愿者协会<br>微信公众号TUSTDX志协</p>
  </alert>
</template>

<script>
import { Cell, XHeader, Actionsheet, Alert, Divider, Group } from 'vux/src/components'

export default {
  components: {
    Cell,
    XHeader,
    Actionsheet,
    Alert,
    Divider,
    Group
  },
  data () {
    return {
      menus: {
        menu1: '提交新物品',
        me: '个人中心',
        menu2: '联系我们'
      },
      showMenus: false,
      show: false,
      items: []
    }
  },
  created () {
    let Items = window.Bmob.Object.extend('items')
    let query = new window.Bmob.Query(Items)
    query.equalTo('status', 0)
    // 查询所有数据
    query.find().then((results) => {
      let temp = []
      console.log('共查询到 ' + results.length + ' 条记录')
      for (let i = 0; i < results.length; i++) {
        let object = results[i]
        let tmp = {
          id: object.id,
          name: object.get('name'),
          location: object.get('location'),
          price: object.get('price')
        }
        temp.push(tmp)
      }
      this.items = temp
    }, (error) => {
      console.log('查询失败: ' + error.code + ' ' + error.message)
    })
  },
  methods: {
    click (key) {
      if (key === 'menu1') {
        this.$router.go('/new')
      } else if (key === 'me') {
        this.$router.go('/user')
      } else if (key === 'menu2') {
        this.show = true
        console.log('Show Feedback')
      } else {
        console.log(key)
      }
    },
    getlink (id) {
      return '/detail/' + id
    }
  }
}
</script>


