<template>
  <x-header :left-options="{showBack: false}">个人中心</x-header>
  <template v-if="isLogin">
    <blur :blur-amount=40 :url="image._url">
      <p class="center">
        <img :src="image._url">
      </p>
      <p class="center">{{ user.attributes.username }}</p>
    </blur>
    <group title="提交的物品">
      <template v-for="item in items">
        <cell :title="item.name" :value="getStatus(item.status)" is-link :link="getlink(item.id)"></cell>
      </template>
      <x-button type="default" text="返回首页" @click="$router.go('/')"></x-button>
    </group>
    <x-button type="warn" text="登出" @click="logout"></x-button>
  </template>
  <template v-else>
    <x-input title="用户名" name="username" placeholder="请输入用户名" :value.sync="username"></x-input>
    <x-input title="密码" name="password" type="password" placeholder="请输入密码" :value.sync="password"></x-input>
    <x-button type="primary" :text="btnText" :disabled="isDisabled" @click="click"></x-button>
    <x-button type="default" text="注册新用户" @click="reg"></x-button>
    <toast :show.sync="showSuccess">登录成功</toast>
    <toast :show.sync="showCancel" type="cancel">登录失败</toast>
  </template>
</template>

<script>
import { Cell, XHeader, XInput, XButton, Toast, Group, Blur } from 'vux/src/components'

export default {
  components: {
    Cell,
    XHeader,
    XInput,
    XButton,
    Toast,
    Group,
    Blur
  },
  data () {
    return {
      btnText: '登录',
      username: '',
      password: '',
      isDisabled: false,
      showCancel: false,
      showSuccess: false,
      isLogin: false,
      items: [],
      user: {},
      image: {}
    }
  },
  created () {
    if (window.userobj) {
      let Items = window.Bmob.Object.extend('items')
      let query = new window.Bmob.Query(Items)
      query.equalTo('user', window.userobj)
      // 查询所有数据
      query.find().then((results) => {
        let temp = []
        console.log('共查询到 ' + results.length + ' 条记录')
        for (let i = 0; i < results.length; i++) {
          let object = results[i]
          let tmp = {
            id: object.id,
            name: object.get('name'),
            status: object.get('status')
          }
          temp.push(tmp)
        }
        this.items = temp
      }, (error) => {
        console.log('查询失败: ' + error.code + ' ' + error.message)
      })
    } else {
      console.log('No Login')
    }
  },
  ready () {
    if (window.user) {
      this.isLogin = true
      this.user = window.userobj
      let query = new window.Bmob.Query(window.Bmob.User)
      query.get(window.user).then((object) => {
        this.image = object.get('image')
      }, (object, error) => {
        console.log('Query Wrong')
      })
    }
  },
  methods: {
    click: function () {
      this.isDisabled = true
      this.btnText = '登录中...'
      window.Bmob.User.logIn(this.username, this.password).then((user) => {
        this.showSuccess = true
        this.isDisabled = false
        this.btnText = '登录'
        this.isLogin = true
        this.user = user
        window.userobj = user
        window.user = user.id
        this.$router.go('/')
      }, (user, error) => {
        this.showCancel = true
        this.isDisabled = false
        this.btnText = '登录'
        this.isLogin = false
        this.user = false
        window.user = false
        window.userobj = false
        console.log('Error!' + error)
      })
    },
    reg: function () {
      this.$router.go('/reg')
    },
    logout: function () {
      window.user = ''
      window.Bmob.User.logOut()
      this.user = false
      this.isLogin = false
      window.userobj = false
      this.$router.go('/')
    },
    getStatus: function (key) {
      if (key === 0) {
        return '待交易'
      } else if (key === 1) {
        return '已完成交易'
      } else if (key === 2) {
        return '已提交回收'
      } else {
        return '状态异常'
      }
    },
    getlink (id) {
      return '/detail/' + id
    }
  }
}
</script>

<style>
  .center {
  text-align: center;
  padding-top: 20px;
  color: #fff;
  font-size: 18px;
}
.center img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid #ececec;
}
</style>


